.main-container{
    width: 100vw;
    height: 100vh;
    background-color: #d8dde9;
    display: flex;
    justify-content: center;
    align-items: center;
}

.card{
width: 50%;
background-color: #fff;
border-radius: 15px;
display: flex;
justify-content: center;
align-items: center;
max-height: 60rem;
flex-direction: column;
height: 70%;
}

.card > *{
    margin: 0 1px;
    width: 50%;
    height: 3rem;
}

.input{
    width: 100%;
    height: 100%;
    padding: 0 10px 0 3rem;
    margin: 0;
    border: 1px solid #e9e9e9;
    border-radius: 5px;
    font-size: 1rem;
outline: unset;
transition: border 200ms;
}

.input:focus{
    border: 1px solid #96959f;
}

.title{
    margin-bottom: 1rem;
padding-right: .5rem;
}

.input-container{
display: flex;
flex-direction: column;
}

.input-main{
    width: 100%;
    height: 100%;
position: relative;
display: flex;
}

.icon{
position: absolute;
width: 1.5rem;
height: 1.5rem;
top: 30%;
left: 10px;
background-size: cover;
}

.result{
margin-top: 3rem;
display: flex;
justify-content: center;
}

.button-container{
margin-top: 1rem;
}

.button:active{
background-color: #1d2a66;
}

.button:hover{
background-color: #2c4095;
}

.button{
width: 100%;
height: 90%;
border: unset;
padding: unset;
border-radius: 15px;
background-color: #3c57c9;
cursor: pointer;
font-size: 1rem;
font-weight: bold;
color: #fff;
transition: background-color 300ms;
}

.result-confirm{
    color: #139313;
}

.result-error{
    color: #ac0808;
}

.result-info{
    color: #c8c502;
}

body{
    box-sizing: border-box;
    direction: rtl;
    margin: 0;
}

@media(max-width: 600px){
    .card{
        width: 90%;
    }
}
